.new-user-form {
  position: relative;
  background-color: rgb(214, 213, 210);
  padding: 15px;
  border: 2px solid black;
}

@media (min-width: 560px) {
  .new-user-form {
    width: 510px;
    margin: 0 auto;
  }
}

.new-user-form__form-title {
  text-align: center;
}

.new-user-form__label {
  display: flex;
  justify-content: space-between;
  margin: 5px 10px;
}

.new-user-form__submit {
  margin: 5px;
}
